}

.scroll-top-btn.is-visible {
    right: 1rem;
}

/* ==========================================
    COUNTERS
========================================== */

.counter {
    background: #171c28;
    position: relative;
    border-radius: 0.5rem;
}

.counter::before {
    content: '';
    width: calc(100% - 1rem);
    height: 3rem;
    border-radius: 0.5rem;
    position: absolute;
    top: 0.5rem;
    left: 0.5rem;
    background: #131722;
}

.counter-item {
    width: 25%;
    z-index: 10;
    position: relative;
}

.counter-item .num {
    width: 100%;
    height: 3rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-pack: center;
    justify-content: center;
    margin: 0 auto;
    position: relative;
    color: #17a2b8;
}

.counter-item .num::before {
    content: '';
    position: absolute;
    top: 50%;
    right: -3px;
    display: block;
    line-height: 0;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    color: #667585;
}

.counter-item:last-of-type .num::before {
    display: none;
}

